.dropdown-menu-container {
  background-color: #343A40;
  color: #ffffff;
  border: none;
  border: 0;
  font-size: 20px;
  height: 50px;
  width: 100px;
  -webkit-appearance: none;
}

select {
  -webkit-border-radius: 0;
  border-radius: 0;
  border: 0;
  outline: 1px solid #343A40;
  outline-offset: -1px;
}

